.main {
  max-width: 980px;
  margin: 0px auto;
  padding: 0px 15px;
  min-height: 100vh;
}

.header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin: 5px 0px;
}

.title {
  color: #11dee6;
  font-size: 40px;
  font-weight: 400;
}

.notes {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(3, minmax(300px, 1fr));
}


@media screen and (max-width: 936px) {
  .notes {
    grid-template-columns: repeat(2, minmax(300px, 1fr));
  }
  
}

@media screen and (max-width: 660px) {
  .notes {
    grid-template-columns: repeat(1, minmax(300px, 1fr));
  }
  
}
body{
  display: grid;
  height: 100vh;
  place-items: center;
  background: #2b2b2b;
  background-size: cover;
}

.checkbox{
  height: 32px;
  width: 64px;
  background: #222;
  appearance: none;
  margin-top: 20px;
  border-radius: 16px;
  box-shadow: inset 0 8px 60px rgba(0,0,0,0.1),
              inset 0 8px 8px rgba(0,0,0,0.1),
              inset 0 -4px 4px rgba(0,0,0,0.1);
  position: relative;
  outline: none;
  cursor: pointer;
  transition: 0.5s;            
}

.checkbox::before{
  height: 32px;
  width: 32px;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  background: linear-gradient(to bottom,#444,#222);
  border-radius: 32px;
  transform: scale(0.9);
  transition: 0.5s;
  box-shadow: 0 8px 40px rgba(0,0,0,0.5),
              inset 4px 4px rgba(255,255,255,0.2),
              inset -4px 4px rgba(255,255,255,0.2);
}

.checkbox:checked{
  background: #fff;
  box-shadow: inset 0 2px 60px rgba(0,0,0,0.1),
              inset 0 2px 8px rgba(0,0,0,0.1),
              inset 0 -4px 4px rgba(0,0,0,0.05);
}

.checkbox:checked::before{
  left: 32px;
  box-shadow: 0 8px 20px rgba(0,0,0,0.1),
              inset 4px 4px rgba(255,255,255,1),
              inset -4px 4px rgba(255,255,255,1);
  background: linear-gradient(to bottom,#eaeaea,#f9f9f9);            
}

.checkbox::after{
  content: "";
  position: absolute;
  min-height: 100vh;
  min-width: 100vw;
  background: #2b2b2b;
  top: 0px;
  left: 0px;
  z-index: -1;
  transform: translate(-16%,-3%);
  transition: 0.5s;
}

.checkbox:checked::after{
  background: #f8f8f8;
  background-size: cover;
}